import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
export default {
	components: {
		uniSearchBar,
		uniList,
		uniListItem
	},
	data() {
		return {
			title: '产品',
			productCategory: null,
			datas: null,
			searchDatas: null,
			searchKey: '',
			curSelectType: '所有分类'
		}
	},
	onShow() {
		this.productCategory = uni.getStorageSync('productCategory')
		this.datas = uni.getStorageSync('productList')
		this.searchDatas = this.datas
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleAdd() {
			uni.navigateTo({
				url: './add/add'
			})
		},
		handleEdit(val) {
			uni.navigateTo({
				url: './edit/edit?item=' + JSON.stringify(val)
			})
		},
		handleSelectType(type) {
			this.curSelectType = type
			if (type !== '所有分类') {
				this.searchDatas = this.datas.filter((item) => {
					return item.productcategory == type
				})
			} else {
				this.searchDatas = this.datas
			}
		},
		handleSearch(val) {
			if (val.value) {
				this.searchDatas = this.datas.filter((item) => {
					return item.productname.indexOf(val.value) !== -1 || item.querycode.indexOf(val.value) !== -1
				})
			} else {
				this.searchDatas = this.datas
			}
		}
	}
}
